
.icon {
     width: 3.5em; height: 3.5em;
     vertical-align: -0.15em;
     fill: currentColor;
     overflow: hidden;
}
main{
  width: 1280px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.navibar{
  width: 100%;
  position: fixed;
  background:rgb(60,174,162);
  height:70px;
}
.home{
  background:rgb(60,174,162);
  height:580px;
}
.about{
  width:1280px;
  margin:100px auto;
}
.work{
  width:100%;
  background:rgb(247,247,247);
}
.contact{
     width:100%;
     background:rgb(60,174,162);
}
.foot{
  padding:30px 0;
  background:rgb(256,256,247);
}
#gotop svg{
    color: rgb(181,181,181);
    position: fixed;
    bottom:60px;
    right: 50px; 
    cursor: pointer;
}
#gotop i:hover{
    opacity: 0.8;
}




