
@-webkit-keyframes type{
from { width: 0;
}
}
@keyframes type{
from { width: 0;
}
}
#home{
	width:1280px;
	color:white;
	display:-webkit-inline-box;
	display:-ms-inline-flexbox;
	display:inline-flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.pic{
	height:580px;
	width:1280px;
	background:url(https://cdn.dribbble.com/users/31752/screenshots/1613357/computer_desk.png) no-repeat ;
}
.type{
	text-align:left;
	height:330px;
	width:500px;
	float:right;
	margin-top:150px;
}
.type h1 {
		font: bold 200% Consolas, Monaco, monospace;
		border-right: .1em solid;
		width: 16.5em; /* fallback */
		width: 30ch; /* # of chars */
		margin: 2em 1em;
		white-space: nowrap;
		overflow: hidden;
		-webkit-animation: typing 10s steps(30, end), /* # of steps = # of chars */
					blink-caret .5s step-end infinite alternate;
		        animation: typing 10s steps(30, end), /* # of steps = # of chars */
					blink-caret .5s step-end infinite alternate;
}
@-webkit-keyframes typing {
from { width: 0;
}
}
@keyframes typing {
from { width: 0;
}
}
@-webkit-keyframes blink-caret {
50% { border-color: transparent;
}
}
@keyframes blink-caret {
50% { border-color: transparent;
}
}
	



