
#navbar{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	color:white;
}
.navheader{
 float:left;
 margin:20px 0;
 font-size:24px;
}
.navlist{
float:right;
}
.navlist>ul>li{
display:inline-block;
margin:25px 20px;
font-size:16px;
}
.navlist>ul>li:hover{
	cursor:pointer;
}	
	




