
.anno{
	margin:30px auto;
	width:1280px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
 	-webkit-box-pack:justify;
 	    -ms-flex-pack:justify;
 	        justify-content:space-between;
 	-webkit-box-align:center;
 	    -ms-flex-align:center;
 	        align-items:center;
}
.power{
	font-size:20px;
}
.logo{
	position:relative;
	left:-10px;
}
.logo >a{
	padding: 0 18px;
}
.logo >a:hover {
	color:rgb(60,174,162);
}
.cha{
	cursor:pointer;
	color:rgb(60,174,162);
	font-size:20px;
}
.cha > a:hover{
	color: rgb(29,29,29);
}
.download> button{
	border-radius:5px;
	margin-bottom:25px;
	height:60px;
	width:250px;
	background:rgb(241,196,15);
	font-size:20px;
}
.download> button >a{
	text-decoration: none;
}
.download> button:hover{
 color:white;
}



