
.contactway{
	height: 335px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-ms-flex-line-pack: center;
	    align-content: center;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
}
.contactway .icon{
	height: 30px;
}
.contactme{
	padding:100px 0;
	margin:0 auto;
	width:1280px;
    color:white;
}
.layout{
 margin:16px 8px;
}
.title{
	padding:30px auto;
}
.info{
 	display:-webkit-box;
 	display:-ms-flexbox;
 	display:flex;
 	-webkit-box-pack:justify;
 	    -ms-flex-pack:justify;
 	        justify-content:space-between;
}
.message {
 	
 	width:450px;
}
.message >button{
 margin-top:16px;
 height:50px;
 width:400px;
 border-radius:5px;
 border:2px solid #292929;
 background:rgb(60,174,162);
}
.message >button:hover{
 border:2px solid white;
}



